let userScore = 0;
let compuetrScore = 0;
let round = 0;
const result = document.getElementsByClassName('current-result')[0];
const rockBtn = document.getElementsByClassName('rock')[0];
const paperBtn = document.getElementsByClassName('paper')[0];
const scissorsBtn = document.getElementsByClassName('scissors')[0];
let score = '';

function getComputerChoise() {
  const choises = ['rock', 'paper', 'scissors'];
  const randomNum = Math.floor(Math.random()*3);
  return choises[randomNum];
}

function showResult() {
  result.innerHTML = `Round ${round}, ${userChoice} vs. ${computerChoise}, ${score} `
}


function game(userChoice) {
  const computerChoise = getComputerChoise();
  switch (userChoice + computerChoise) {
    case 'rockscissors':
    case 'paperrock':
    case 'scissorspapper':
      userScore++;
      score = `You've Won!`;
      showResult();
      break;
    case 'scissorsrock':
    case 'rockpaper':
    case 'paperscissors':

      break;
    case 'scissorsscissors':
    case 'paperpaper':
    case 'rockrock':

      break;
  }
}

  rockBtn.addEventListener('click', function () {
    game('rock');
  });

  paperBtn.addEventListener('click', function() {
    game('paper');
  });

  scissorsBtn.addEventListener('click', function() {
    game('scissors');
  });
